<template>
  <div class="panel panel-store">
    <div class="panel-title">
      <div class="avator" @click.stop.prevent="showLeftMenuPopup=true">
        <img :src="userInfo.picture" alt="">
      </div>
      <div class="address" @click.stop.prevent="selectCity">
        <span>{{cityName}}</span>
      </div>
      <van-search placeholder="一秒锁定您的商户/课程" readonly disabled @click.native.stop="searchStore" />
    </div>
    <div class="panel-body">
      <div class="card" v-for="(item, index) in storeList" :key="index" @click.stop.prevent="goTo(item.store.storeId, 'store')">
        <div class="card-title">
          <img :src="'./img/course/logo-kc-1.png' || item.store.picture" alt="store logo">
          <div class="store-detail">
            <div>
              <h4 class="ellipsis">{{item.store.storeName}}</h4>
              <span>{{item.store.distance}}</span>
            </div>
            <div>
              <b>共 {{item.store.productNum}} 门课程</b>
              <em class="ellipsis">{{item.store.address}}</em>
            </div>
          </div>
        </div>
        <ul class="card-body van-hairline--top" @click.stop>
          <li v-for="(course, i) in item.merchandise" :key="i" @click.stop.prevent="goTo(course.id, 'course')">
            <h5 class="ellipsis">{{course.name}}</h5>
            <div class="flex-wrap"><b>可选</b><span class="mid-flex" v-for="period in course.payType.split(',')" :key="period">{{period}}</span></div>
          </li>
        </ul>
      </div>
    </div>
    <van-popup v-model="showLeftMenuPopup" position="left">
      <left-menu></left-menu>
    </van-popup>
  </div>
</template>

<script>
import leftMenu from '@/components/left-menu.vue';
import { queryStoreList } from '@/api/index.js';

export default {
  name: 'store',
  components: {
    leftMenu,
  },
  computed: {
    userInfo() {
      return this.$store.state.userInfo;
    },
  },
  data() {
    return {
      cityName: '上海',
      storeList: [
        {
          store: {},
          merchandise: [],
        },
      ],
      page: 1,
      size: 10,
      showLeftMenuPopup: false,
    };
  },
  created() {
    this.queryData();
  },
  methods: {
    queryData() {
      console.log(Date.now());
      queryStoreList(this.page, this.size)
        .then((result) => {
          // TODO 无线加载
          if (Array.isArray(result.list)) {
            this.storeList = result.list;
          }
        })
        .catch(console.error);
    },
    selectCity() {
      // this.$router.push('/select-city');
    },
    searchStore() {
      // this.$router.push('/search-store');
    },
    goTo(id, flag) {
      if (flag === 'store') {
        this.$router.push(`/store-detail/${id}`);
      } else if (flag === 'course') {
        this.$router.push(`/order-course/${id}`);
      }
    },
  },
};
</script>

